import { Component, OnInit } from '@angular/core';


@Component({
  selector: 'app-nav',
  templateUrl: './nav.component.html',
  styleUrls: ['./nav.component.css'],
})
export class NavComponent implements OnInit {

  
  // Lower volume and turn on/off
  vol = 0.2;
  music = true;
  turnMusic(){
    this.music = !this.music;
    if(this.music) this.vol = 0.2;
    else this.vol = 0;
  }

  // Back to top
  backToTop(){
    window.scrollTo({
      top: 0,
    })
  }

  // Show menu
  menu = false;
  showMenu() {
    this.menu = !this.menu;
  }

  constructor() { }


  ngOnInit(): void {

    // Back to top hide/show and change nav color
    window.addEventListener("scroll", function () {

        //active link
        let act = document.querySelectorAll('li');
        let bodyBg = document.querySelector<HTMLElement>("body");
        let about = document.querySelector<HTMLElement>("app-about").offsetTop;
        let project = document.querySelector<HTMLElement>("app-project").offsetTop;
        let contact = document.querySelector<HTMLElement>("app-contact").offsetTop;

        if(window.scrollY < screen.height - 200) 
        act[0].classList.add("active__link");
        else act[0].classList.remove("active__link");

        if(window.scrollY > about + 200 && window.scrollY < project - 200) 
        act[1].classList.add("active__link");
        else act[1].classList.remove("active__link");

        if(window.scrollY > project + 200 && window.scrollY < contact - 200) 
        act[2].classList.add("active__link");
        else act[2].classList.remove("active__link");

        if(window.scrollY > contact - 200) 
        act[3].classList.add("active__link");
        else act[3].classList.remove("active__link");


        let btn = document.querySelector<HTMLElement>('.scrollTop');
        let nav1 = document.querySelector<HTMLElement>('.nav__link');
        let nav2 = document.querySelector<HTMLElement>('.nav__menu');
        let nav3 = document.querySelector<HTMLElement>('.nav__music');

        //show to Top
        if(window.scrollY > 400) btn.classList.add("toTop");
        else btn.classList.remove("toTop");

        // Change page background
        if(window.scrollY < 600)
        bodyBg.classList.add("black");
        else if( window.scrollY > (bodyBg.offsetHeight - screen.height))
        bodyBg.classList.add("black");
        else 
        bodyBg.classList.remove("black");

        // change nav color
        if(window.scrollY > 600 &&  window.scrollY < (bodyBg.offsetHeight - screen.height)){
          nav1.classList.add("black");
          nav2.classList.add("black");
          nav3.classList.add("black");
          btn.classList.add("black");
        }
        else{
          nav1.classList.remove("black");
          nav2.classList.remove("black");
          nav3.classList.remove("black");
          btn.classList.remove("black");
        }

    });

    
  }
}
